<template>
	<div id="reserve-goods">
		<store-header :store="store"></store-header>
		<!-- 标签 -->
		<div class="store-tabs flex-lr van-hairline--bottom">
			<div class="tab-item active">商品</div>
			<router-link class="tab-item" :to="util.getUrl({path: '/pages/store/comment?sid=' + store.id})" tag="div">评价</router-link>
			<router-link class="tab-item" :to="util.getUrl({path: '/pages/store/index?sid=' + store.id})" tag="div">商家</router-link>
		</div>
		<div class="goods-container">
			<van-row>
				<van-col span="6" :style="{height: vanColHeight +'px'}">
					<van-badge-group :active-key="categorySelectedIndex" class="tabs-list">
						<template v-for="(item, index) in categoryAll">
							<van-badge :title="item.title" @click="onToggleCategory(item.id, index, '')" v-if="item.total > 0" :info="item.total" />
							<van-badge :title="item.title" @click="onToggleCategory(item.id, index, '')" v-else />
						</template>
					</van-badge-group>
				</van-col>
				<van-col span="18" :style="{height: vanColHeight +'px'}">
					<van-list :finished="goodsItem.loaded"  @load="onGetGoods" :offset="50" :immediate-check="false">
						<!-- 商家代金券 -->
						<div class='coupon-show-container' v-if="coupon.id > 0 && couponStatus == 1">
							<div class='coupon-price'>
								¥<div>{{coupon.price}}</div>
							</div>
							<div class='coupon-detail'>
								<div class='coupon-title'>商家代金券</div>
								<div class='coupon-desc'>内含{{coupon.num}}张券</div>
							</div>
							<div class='coupon-fetch' @click="onCollectCoupon">去领取</div>
						</div>
						<!-- 门店海报 -->
						<div class="banner" v-if="0 && shopPage">
							<template v-for="(shopPageItem, shopPageKey) in shopPage">
								<router-link class="banner-item" v-if="shopPageItem.wxapp_link" :to="util.getUrl({path: 'shopPageItem.wxapp_link'})" >
									<img :src="shopPageItem.thumb">
								</router-link>
								<router-link class="banner-item" v-else-if="shopPageItem.goodsLength == 1" :to="util.getUrl({path: './goodsDetail', query: {sid: store.id, id: shopPageItem.goods[0]}})" >
									<img :src="shopPageItem.thumb">
								</router-link>
								<router-link class="banner-item" v-else-if="shopPageItem.goodsLength > 1" :to="util.getUrl({path: './shopPage', query: {sid: store.id, shopPageKey: shopPageKey}})" >
									<img :src="shopPageItem.thumb">
								</router-link>
							</template>
						</div>
						<!-- 二级分类 -->
						<template v-if="categorySelected.child">
							<div class="tag2-list" v-if="categorySelected.child.length > 0">
								<div class="tag2-item" v-for="(item, index) in categorySelected.child" :class="{selected: index == childSelectedIndex}" @click="onToggleCategory(item.id, index, 'child')">
									{{item.title}}
									<div :class="{dot: item.total}" v-if="item.total > 0"></div>
								</div>
							</div>
						</template>
						<div class="title border-1px-t">
							<div class='title-line' style="{'border-color': '#6BBA50'}"></div>
							{{categorySelected.child ? categorySelected.child[childSelectedIndex].title : categorySelected.title}}
							<div class="title-rank">
								<div class="sales " :class="{'selected': (goodsItem.orderby.type == 'sailed')}" @click="onOrderby('sailed', 'desc')">销量</div>
								<div class="shu">|</div>
								<div class="price-container">
									<div class="price">价格</div>
									<div class="triangle" @click="onOrderby('price', (goodsItem.orderby.value == 'desc' ? 'asc' : 'desc'))">
										<div class="triangle-top" :class="{selected: (goodsItem.orderby.type == 'price' && goodsItem.orderby.value == 'asc')}"></div>
										<div class="triangle-bottom " :class="{selected: (goodsItem.orderby.type == 'price' && goodsItem.orderby.value == 'desc')}"></div>
									</div>
								</div>
							</div>
						</div>
						<!-- 单列模板 -->
						<template v-if="template == 2">
							<div class="goods clearfix border-1px-b" v-if="goodsItem.data.length > 0" v-for="(item, index) in goodsItem.data">
								<router-link class="image-box" :to="util.getUrl({path: './goodsDetail', query: {id: item.id, sid: store.id}})">
									<img class="pic" v-lazy="{src: item.thumb, loading: ''}">
									<div class="label" v-if="item.label">{{item.label}}</div>
								</router-link>
								<div class='shop-info clearfix'>
									<div class="name ellipsis">{{item.title}}</div>
									<div class="description">{{item.content}}</div>
									<div class="sold">已售{{item.sailed}} 赞{{item.comment_good}}</div>
									<div class="price">
										<div class="now-price">
											<div class="price-icon">￥</div>{{item.price}}
										</div>
										<div class='old-price' v-if="item.old_price">
											<div class="price-icon">￥</div>{{item.old_price}}
										</div>
									</div>
									<div class='discount'>
										<template v-if="item.discount">
											<van-icon name='tag'></van-icon>
											{{item.discount}}折 限{{item.max_buy_limit}}份
										</template>
									</div>
								</div>
								<goods-handle
									:store="store"
									:goods="item"
									:optionId="0"
									from="list">
								</goods-handle>
								<div style="clear:both"></div>
							</div>
						</template>
						<load v-if="goodsItem.empty == 1" type="loaded" text="暂无商品"></load>
						<load v-else-if="goodsItem.loaded" type="loaded" text="已经到底了"></load>
						<load v-if="goodsLoading" type="loading"></load>
					</van-list>
				</van-col>
			</van-row>
		</div>
		<!-- 购物车部分 -->
		<store-cart
			:show="activetabs == 0"
			:store="store"
			:cart="cart"
			:table_category="table_category"
			from='reserve'>
		</store-cart>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import PublicFooter from '@/components/footer'
	import Load from '@/components/load'
	import StoreCart from '@/components/storeCart'
	import GoodsHandle from '@/components/goodsHandle'
	import StoreHeader from '@/components/storeHeader'
	export default {
		name: 'reserveGoods',
		data() {
			return {
				store:{
					is_rest: 0,
					activity: {
						num: 0
					}
				},
				activetabs: 0,
				swiper_dots: false,
				tabswiperable: true,
				is_rest: 0,
				status: {
					options_show: false,
					not_in_time_show: false,
					cart_show: false,
				},
				vanColHeight: 0,
				commentHeight: 0,
				loading: false,
				goodsLoading: false,
				finished: false,
				categorySelectedIndex: 0,
				categorySelectedId: 0,
				categoryAll:[],
				categorySelected: [],
				childSelectedIndex: 0,
				childSelectedId: 0,
				goodsAll: [],
				goodsItem: {
					orderby: {
						type: '',
						value: ''
					}
				},
				recommend_stores: {},
				coupon: [],
				shopPage: {},
				cart: {
					num: 0,
				},
				template: 2,
				table_category: {},
			}
		},
		components: {
			PublicFooter,
			Load,
			StoreCart,
			GoodsHandle,
			StoreHeader
		},
		methods: {
			...mapMutations([
				'replaceStore', 'replaceCart'
			]),

			onToggleCategory(cid, index, from) {
				var childIndex = 0;
				if(from == 'child') {
					childIndex = index;
					this.childSelectedIndex = childIndex;
					this.categoryAll[this.categorySelectedIndex]['childSelectedIndex'] = childIndex;
					this.categorySelected = this.categoryAll[this.categorySelectedIndex];
				} else {
					if(this.categoryAll[index]['childSelectedIndex'] > 0) {
						childIndex = this.categoryAll[index].childSelectedIndex;
					}
					this.categorySelected = this.categoryAll[index];
					this.categorySelectedIndex = index;
					this.categoryAll[index]['childSelectedIndex'] = childIndex;
				}
				this.categorySelectedId = this.categorySelected.id;
				this.childSelectedId = 0;
				if(this.categorySelected.child) {
					this.childSelectedId = this.categorySelected.child[childIndex]['id'];
				}
				this.onGetGoods();
			},

			onGetGoods() {
				if(this.goodsLoading) {
					return false;
				}
				if(this.goodsAll[this.categorySelectedIndex]) {
					this.goodsItem = this.goodsAll[this.categorySelectedIndex][this.childSelectedIndex];
				} else {
					this.goodsItem = this.goodsAll[this.categorySelectedIndex];
				}
				if(!this.goodsItem) {
					this.goodsItem = {
						page: 1,
						psize: 5,
						empty: 0,
						loaded: false,
						data: [],
						orderby: {
							type: '',
							value: ''
						}
					};
				} else {
					if(this.goodsItem.empty) {
						return false;
					}
					if(this.goodsItem.loaded) {
						console.log('商品全部加载完成');
						return false;
					}
				}
				this.goodsLoading = true;
				alert(this.store.id);
				this.util.request({
					url: 'wmall/store/reserve/goods',
					data: {
						sid: this.store.id,
						page: this.goodsItem.page,
						psize: this.goodsItem.psize,
						cid: this.categorySelectedId,
						child_id: this.childSelectedId,
						type: this.goodsItem.orderby.type,
						value: this.goodsItem.orderby.value
					},
				}).then((res) => {
					this.goodsLoading = false;
					var result = res.data.message.message;
					this.goodsItem.data = [...this.goodsItem.data, ...result.goods];
					this.goodsItem.page++;
					if(result.goods.length < this.goodsItem.psize) {
						this.goodsItem.loaded = true;
						if(!result.goods.length) {
							this.goodsItem.empty = 1;
						}
					}
					if(!this.goodsAll[this.categorySelectedIndex]) {
						this.goodsAll[this.categorySelectedIndex] = [];
					}
					this.goodsAll[this.categorySelectedIndex][this.childSelectedIndex] = this.goodsItem;
				})
			},

			onOrderby(type, value) {
				this.goodsItem = {
					page: 1,
					psize: 5,
					empty: 0,
					data: [],
					orderby: {
						type: type,
						value: value
					}
				};
				this.goodsAll[this.categorySelectedIndex][this.childSelectedIndex] = this.goodsItem;
				this.onGetGoods();
			},

			onLoad() {
				console.log('---');
				console.log(this.reserveExtra);
				this.util.request({
					url: 'wmall/store/reserve/goodsIndex',
					data: {
						table_cid: this.reserveExtra.cid,
						forceLocation: '1',
						sid: '3'
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					}
					result = result.message;
					this.goodsItem = {
						page: 2,
						psize: 5,
						empty: 0,
						data: result.goods,
						orderby: {
							type: '',
							value: ''
						}
					};
					if(this.goodsItem.data.length < this.goodsItem.psize) {
						this.goodsItem.loaded = true;
						if(!this.goodsItem.data.length) {
							this.goodsItem.empty = 1;
						}
					}
					this.categorySelectedId = result.cid;
					this.childSelectedId = result.child_id;
					this.store = result.store;
					//this.shopPage = result.store.data.shopPage;
					this.coupon = result.coupon;
					this.categoryAll = result.category;
					this.table_category = result.table_category;
					this.is_rest = result.store.is_rest;
					//this.recommend_stores = result.recommend_stores;
					this.goodsAll[this.categorySelectedIndex] = [];
					this.goodsAll[this.categorySelectedIndex][this.childSelectedIndex] = this.goodsItem;
					this.categorySelected =  result.category[this.categorySelectedIndex];
					//this.template = result.template;

					this.replaceStore(result.store);
					this.replaceCart(result.cart.message.cart);
				})
			},
		},
		watch: {
			icart() {
				var cartgoods = this.icart.data;
				var categoryAllTemp = this.categoryAll;
				if(cartgoods) {
					for(var c in categoryAllTemp) {
						categoryAllTemp[c]['total'] = 0;
						if(categoryAllTemp[c]['child']) {
							for(var index in categoryAllTemp[c]['child']) {
								categoryAllTemp[c]['child'][index]['total'] = 0;
								var childtotal = 0;
								var total = 0;
								for(var i in cartgoods) {
									for(var j in cartgoods[i]) {
										if(cartgoods[i][j]['cid'] == categoryAllTemp[c]['id']) {
											total += cartgoods[i][j]['num'];
											categoryAllTemp[c]['total'] = total;
											if(cartgoods[i][j]['child_id'] == categoryAllTemp[c]['child'][index]['id']) {
												childtotal += cartgoods[i][j]['num'];
												categoryAllTemp[c]['child'][index]['total'] = childtotal;
											}
										}
									}
								}
							}
						} else {
							var total = 0;
							for(var i in cartgoods) {
								for(var j in cartgoods[i]) {
									if(cartgoods[i][j]['cid'] == categoryAllTemp[c]['id']) {
										total += cartgoods[i][j]['num'];
										categoryAllTemp[c]['total'] = total;
									}
								}
							}
						}
					}
				} else {
					for(var c in categoryAllTemp) {
						categoryAllTemp[c]['total'] = 0;
						if(categoryAllTemp[c]['child']) {
							for(var index in categoryAllTemp[c]['child']) {
								categoryAllTemp[c]['child'][index]['total'] = 0;
							}
						}
					}
				}
				this.categoryAll = categoryAllTemp;
			},
		},

		computed: {
			...mapState([
				'istore', 'icart', 'reserveExtra',
			]),
		},
		mounted () {
			this.onLoad();
			let bodyHeight = document.documentElement.clientHeight;
			let headerHeight = document.getElementsByClassName('header')[0].clientHeight;
			let tabsHeight =document.getElementsByClassName('store-tabs')[0].clientHeight;
			this.vanColHeight = bodyHeight-headerHeight-tabsHeight-50;
		}
	}
</script>

<style>
	@import '../../../static/css/diy.css';
	#reserve-goods .van-col{
		overflow-y: auto;
		background: #fff;
	}
	#reserve-goods .van-col::-webkit-scrollbar {
		width: 0px;
		height: 0px;
	}
	#reserve-goods .diy-store-list::-webkit-scrollbar {
		width: 0px;
		height: 0px;
	}
	#reserve-goods .van-cell__left-icon{
		font-size: 20px;
	}
	#reserve-goods .store-tabs{
		width: 100%;
		height: 45px;
		line-height: 45px;
		background-color: #fff;
		color: #333;
	}
	#reserve-goods .store-tabs .tab-item{
		width: 40%;
		height: 100%;
		text-align: center;
		font-size: 14px;
		position: relative;
	}
	#reserve-goods .store-tabs .tab-item.active{
		color: #f44;
	}
	#reserve-goods .store-tabs .tab-item.active:after{
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 0;
		border-bottom: 2px solid #f44;
	}
	/* 分隔线 */
	#reserve-goods .fg-line{
		display: inline-block;
		margin: 5px 5px 0 5px;
		width: 0;
		height: 9px;
		font-size: 0;
		border-right: 1px solid #ccc;
	}
	/*商品主体*/
	#reserve-goods .goods-container .tabs-list{
		width: 100%;
		text-align: center;
	}
	#reserve-goods .goods-container .tabs-list .van-badge{
		padding: 15px 6px;
		line-height: 1.5;
	}
	/*门店优惠券*/
	#reserve-goods .coupon-show-container{
		margin: 10px;
		height: 75px;
		background: url(//xs01.meituan.net/waimai_i/img/coupon_bg.41b40907.png) no-repeat;
		background-size: 100% 75px;
	}
	#reserve-goods .coupon-show-container .coupon-price{
		float: left;
		line-height: 75px;
		margin-left: 16px;
		color: #FFD161;
		font-size: 17px;
		text-align: center;
		white-space: nowrap;
	}
	#reserve-goods .coupon-show-container .coupon-price div{
		display: inline-block;
		font-size: 30px;
	}
	#reserve-goods .coupon-show-container .coupon-detail{
		float: left;
		margin: 15px 0 0 4%;
	}
	#reserve-goods .coupon-show-container .coupon-detail .coupon-title{
		line-height: 23px;
		font-size: 16px;
		color: #333;
	}
	#reserve-goods .coupon-show-container .coupon-detail .coupon-desc{
		line-height: 16px;
		font-size: 12px;
		color: #999;
	}
	#reserve-goods .coupon-show-container .coupon-fetch{
		float: right;
		width: 25%;
		line-height: 75px;
		color: #FFD161;
		font-size: 14px;
		text-align: center;
	}
	/*门店海报*/
	#reserve-goods .banner{
		width: 100%;
		box-sizing: border-box;
		margin: 10px 0px;
		padding: 0px 10px;
	}
	#reserve-goods .banner .banner-item{
		display: inline-block;
		width: 100%;
		height: 80px;
		margin-bottom: 10px;
	}
	#reserve-goods .banner .banner-item:last-child{
		margin-bottom: 0px;
	}
	#reserve-goods .banner .banner-item img{
		width: 100%;
		height: 100%;
		border-radius: 4px;
		margin-bottom: -1px;
	}
	/* 二级分类样式 */
	#reserve-goods .tag2-list{
		width: 100%;
		padding-left: 10px;
		box-sizing: border-box;
		display:flex;
		flex-wrap:wrap;
		align-items: flex-end;
		margin-top: 10px;
	}
	#reserve-goods .tag2-list .tag2-item{
		position: relative;
		display:inline-block;
		width:29%;
		height:35px;
		padding:0px 5px;
		box-sizing: border-box;
		background: #F3F3F3;
		text-align:center;
		line-height: 35px;
		font-size:14px;
		margin-right: 10px;
		margin-bottom: 10px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		border-radius: 2px;
	}
	#reserve-goods .tag2-list .tag2-item.selected{
		background: #FFFBF2;
		border:1px solid #EFB848;
		color:#F6AB2F;
		top: 0;
		left:0;
	}
	#reserve-goods .tag2-list .dot{
		width: 7px;
		height: 7px;
		position: absolute;
		top: 7px;
		right: 7px;
		border-radius: 50%;
		background-color: #ff2d4b;
	}
	/*分类名称*/
	#reserve-goods .title{
		color: #333;
		font-size: 14px;
		padding: 5px 8px;
		line-height: 18px;
		background: #F9F9F9;
	}
	#reserve-goods .title .title-line{
		display: inline-block;
		margin: 5px 3px 0 3px;
		width: 0;
		height: 10px;
		border-right: 2px solid #FFD161;
	}
	#reserve-goods .title .title-rank{
		float:right;
		display: flex;
		font-size:12px;
		height:100%;
	}
	#reserve-goods .title .title-rank .selected{
		font-weight: bold;
	}
	#reserve-goods .title .title-rank .shu{
		font-size:6px;
		padding:0px 8px;
		color:#999;
	}
	#reserve-goods .title .title-rank .price-container{
		display: flex;
	}
	#reserve-goods .title .title-rank .price-container .triangle{
		height:100%;
		padding-top:3px;
		padding-left:3px;
	}
	#reserve-goods .title .title-rank .price-container .triangle .triangle-top{
		width: 0;
		height: 0;
		border-left: 4px solid transparent;
		border-right: 4px solid transparent;
		border-bottom: 5px solid #ccc;
		margin-bottom:2px;
	}
	#reserve-goods .title .title-rank .price-container .triangle .triangle-top.selected{
		border-bottom: 5px solid #000;
	}
	#reserve-goods .title .title-rank .price-container .triangle .triangle-bottom{
		width: 0;
		height: 0;
		border-left: 4px solid transparent;
		border-right: 4px solid transparent;
		border-top: 5px solid #ccc;
	}
	#reserve-goods .title .title-rank .price-container .triangle .triangle-bottom.selected{
		border-top: 5px solid #000;
	}

	/*商品*/
	#reserve-goods .goods-container .goods{
		position: relative;
		padding: 10px 8px;
		font-size: 18px;
		line-height: 20px;
	}

	#reserve-goods .goods-container .goods .image-box{
		float: left;
		width: 56px;
		height: 56px;
		display: inline-block;
		margin-right: 10px;
		position: relative;
	}
	#reserve-goods .goods-container .goods .image-box .pic{
		width: 100%;
		height: 100%;
	}
	#reserve-goods .goods-container .goods .image-box .label{
		position: absolute;
		left: 0;
		top: 0;
		font-size: 12px;
		color: #fff;
		background-color: #ff2d4b;
		height: 14px;
		line-height: 14px;
		padding: 0 2px;
	}
	#reserve-goods .goods-container .goods .shop-info{
		float: left;
		max-width: 72%;
		min-height: 55px;
	}
	#reserve-goods .goods-container .goods .shop-info .name{
		color: #3d4145;
		font-size: 16px;
		line-height: 20px;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
		text-overflow: ellipsis;
		font-weight: 500;
	}
	#reserve-goods .goods-container .goods .shop-info .sold{
		color: #8C8C8C;
		font-size: 12px;
		line-height: 1.5;
	}
	#reserve-goods .goods-container .goods .shop-info .description{
		color: #8C8C8C;
		font-size: 10px;
		line-height: 16px;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	#reserve-goods .goods-container .goods .shop-info .price .now-price{
		color: #f45044;
		font-size: 20px;
		line-height: 20px;
		font-weight: bold;
		display: inline-block;
	}
	.goods-container .goods .shop-info .price .now-price .price-icon{
	#reserve-goods 	font-size: 14px;
		display: inline-block;
	}
	#reserve-goods .goods-container .goods .shop-info .price .old-price{
		display: inline-block;
		font-size: 12px;
		text-decoration: line-through;
		color: #8C8C8C;
		font-weight: normal;
		margin-left: 5px;
	}
	#reserve-goods .goods-container .goods .shop-info .price .old-price .price-icon{
		font-size: 10px;
		display: inline-block;
	}
	#reserve-goods .goods-container .goods .shop-info .discount{
		font-size: 12px;
		color: #f45044;
	}

	#reserve-goods .goods-list{
		background: #fff;
		padding-bottom: 20px;
	}
	#reserve-goods .goods-list .goods-item{
		padding-top: 20px;
		padding-bottom: 10px;
		float: left;
		border-right: 1px solid #e0e0e0;
		border-bottom: 1px solid #e0e0e0;
		box-sizing: border-box;
		width: 50%;
		display: inline-block;
	}
	#reserve-goods .goods-list .goods-item .goods-info{
		position: relative;
		padding: 0 15px;
	}
	#reserve-goods .goods-list .goods-item .avatar{
		display: inline-block;
		width: 100%;
		height: 95px;
	}
	#reserve-goods .goods-list .goods-item .avatar img{
		display: inline-block;
		width: 100%;
		height: 100%;
	}
	#reserve-goods .goods-list .goods-item .goods-name{
		font-size: 16px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		margin-top: 5px;
	}
	#reserve-goods .goods-list .goods-item .description{
		font-size: 14px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		color: #606060;
		height: 22px;
	}
	#reserve-goods .goods-list .goods-item .solid{
		font-size: 12px;
		color: #B8B8B8;
		margin-top: 3px;
	}
	#reserve-goods .goods-list .goods-item .solid div{
		display: inline-block;
		margin-left: 5px;
	}
	#reserve-goods .goods-list .goods-item .price{
		font-size: 14px;
		color: #FA4F3F;
		margin-top: 5px;
	}
	#reserve-goods .goods-list .goods-item .price{
		color: #FA4F3F;
		font-size: 14px;
		margin-top: 5px;
		line-height: 20px;
		display: inline-block;
	}
	#reserve-goods .goods-list .goods-item .price div{
		display: inline-block;
		font-size: 20px;
		font-weight: bold;
		line-height:20px;
	}
	#reserve-goods .goods-list .goods-item .old-price{
		display: inline-block;
		font-size: 12px;
		text-decoration: line-through;
		color: #8C8C8C;
		font-weight: normal;
	}
	#reserve-goods .goods-list .goods-item .discount{
		font-size: 12px;
		color: #f45044;
		height: 22px;
	}
	/*店铺休息中推荐*/
	#reserve-goods .recommend-popup{
		width: 100%;
		height: 380px;
		overflow: hidden;
		z-index: 100000!important;
	}
	#reserve-goods .guide-head {
		width: 93%;
		background: #fff;
		padding: 15px 15px;
		text-align: center;
		color: #333;
		height: 100px;
		box-sizing: border-box;
		margin: 0 auto;
	}
	#reserve-goods .guide-head .store-rest {
		font-size: 20px;
		font-weight: 600;
		line-height: 20px;
		overflow: hidden;
		padding-left: 22px;
		text-align: center;
	}
	#reserve-goods .guide-head .store-rest .store-close {
		float: right;
	}
	#reserve-goods .guide-head .store-rest .store-close .van-icon-unfold {
		float: right;
		width: 100%;
		height: 100%;
		text-align: center;
		font-size: 16px;
		line-height: 16px;
		color: #727272;
	}
	#reserve-goods .guide-head .store-start {
		font-size: 16px;
		color: #727272;
		display: block;
	}
	#reserve-goods .guide-head .store-recommended {
		font-size: 16px;
		color: #ccc;
		display: block;
		margin-top: 7px;
	}
	#reserve-goods .diy-store-list{
		width: 100%;
		height: 270px;
		box-sizing: border-box;
		overflow-y: auto;
	}
	/*打烊了*/
	#reserve-goods .rest{
		position: fixed;
		bottom: 0;
		z-index: 20000;
		text-align: center;
		width: 100%;
		height: 50px;
		line-height: 50px;
		background-color: #343434;
		color: #fff;
		font-size: 14px;
	}
	#reserve-goods .goods-list .foodop{
		right: 15px;
		bottom: 22px;
	}
</style>



